body{
  background-color: black;
}

.App{
  width: 100%;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header{
  color: whitesmoke;
  background-color: black;
  display: flex;
  width: 95%;
  flex-direction: column;
  align-items: center;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}
.header h4{
  background-color: black;
  padding: 10px;
  border-radius: 8px;
}

.App-body-1{
  background-color:black;
  color:rgb(194, 194, 194);
  margin: 30px;
  padding: 50px;
  border-radius: 15px;
  padding-bottom: 20px;
}

.App-body-1 p{
  font-size: 10px;
}

.to-do-form{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inputdiv{
  /* width: 90%; */
  height: 30px;
  display: flex;
  margin: 10px auto;
}
.inputlabels{
  width: 6vw;
}

.to-do-form button{
  margin-top: 10px;
  width:80px;
  height:40px;
  align-self: auto;
  margin-left: 20px;
  background-color: rgb(43, 43, 43);
  color: whitesmoke;
  border-radius: 12px;
  border: 0px none;
}


.bottom-body{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  width: 100%;
  color:white;
  padding-bottom: 30px;

}


#header-2{
  border-bottom:rgb(37, 103, 105);
  border-bottom-style: solid;
  padding:2px;
  border-radius: 12px;
  
}

.App-body-2{
  margin-top: 40px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-content: flex-start;
  justify-content: space-evenly;
}

