.top-body{
    background-color:rgb(39, 39, 39);
    width: 30%;
    border-radius: 25px;
    color:whitesmoke;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.body-details{
    border-top-style: solid;
    border-top-color: black;
    width: 60%;
    border-bottom-style: dotted;
    border-bottom-color:black ;
    min-height: 50%;
    
}
.body-div{
    display: flex;
    flex-direction: row;
    align-content: flex-start;
    justify-content: space-between;

}
